.profile-img {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background-size: cover;
  background-position: center;
}

.profile-img:hover .expanded-div {
  height: 30px;
}

.profile-img-dark {
  background-color: #0b0e16;
  box-shadow: 0 0 10px #000;
}

.profile-img-light {
  box-shadow: 0 0 10px rgb(165, 165, 165);
  background-color: #fff;
}

.expanded-div {
  position: absolute;
  font-size: 1rem;
  height: 0px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 20px;
  transition: 0.1s ease-in;
  transition-property: height, transform;
  background: #fff;
  color: #000;
  border-radius: 15px;
  padding: 0 10px;
}

.expanded-div:active {
  transform: scale(0.9);
}

.photo-actions span {
  margin: 0 5px;
  font-weight: bold;
  cursor: pointer;

  border-radius: 10px;
  padding: 5px 10px;
}

.discard {
  color: rgb(204, 30, 30);
  border: 1px solid rgb(204, 30, 30);
}

.save {
  color: rgb(35, 206, 118);
  border: 1px solid rgb(35, 206, 118);
}
