.flex-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.app-profile-view {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 50px;
}

.profile-name {
  font-size: 7rem;
  margin-left: 50px;
}

.profile-email {
  margin-left: 55px;
}

.teams-container {
  margin-left: 30px;
}

.profile-img {
  width: 300px;
  height: 300px;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;

  margin-bottom: 30px;

  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.profile-img-dark {
  background-color: #0b0e16;
  box-shadow: 0 0 10px #000;
}

.profile-img-light {
  box-shadow: 0 0 10px rgb(165, 165, 165);
  background-color: #fff;
}

.file-input {
  display: none;
}

.h3-no-teams {
  font-size: 1.3rem;
  margin: 20px 0 10px 0;
  border: 3px solid red;
  padding: 5px 10px;
  border-radius: 10px;
}

.teams-list {
  display: flex;
  flex-direction: row;
}

.team-mini {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 20px;
  border-radius: 15px;
}

.img-wrap {
  width: 40px;
  height: 40px;
  background-position: center;
  overflow: hidden;
  border-radius: 50%;
  background-size: cover;
}

.team-mini > a {
  margin-left: 10px;
  text-decoration: underline;
  font-weight: bold;
  text-transform: uppercase;
}

.left-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.invitations-h2 {
  margin: 10px 0;
}

.accept-invitation-btn {
  background-color: rgb(86, 212, 47);
  font-size: 0.8rem;
  padding: 5px 10px;
  margin: 0 5px;
}

.deny-invitation-btn {
  background-color: rgb(212, 47, 47);
  font-size: 0.8rem;
  padding: 5px 10px;
  margin: 0 5px;
}
