.call-container {
  position: fixed;
  left: 50%;
  bottom: -120%;
  transform: translateX(-50%) translateY(-50%);
  width: 60%;
  padding: 15px;
  background: #000;
  color: #fff;
  transition: 0.4s ease-in;
  height: 70%;
}

.show-mini {
  bottom: -20%;
}

#localVideo {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 200px;
}

.call-buttons {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 30px;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.call-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #fff;
  color: #000;

  display: flex;
  justify-content: center;
  align-items: center;

  margin: 0 5px;
  cursor: pointer;
  color: #fff;
}
