button {
  padding: 10px 20px;
  margin: 5px 0;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1.2rem;
  border-radius: 10px;
  font-weight: bold;
  font-family: 'Noto Sans', sans-serif;
  color: #eee;
  transition: 0.2s transform;
}

button:active {
  transform: scale(0.95);
}
