.app-header {
  display: flex;
  flex-direction: row;
  padding: 0 30px;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(120, 88, 224);
}

.app-header h1 {
  font-size: 1.5em;
}

.app-header-light {
  color: #fff;
  box-shadow: 0 0 10px rgb(80, 80, 80);
}

.app-header-dark {
  color: #fff;
  box-shadow: 0 0 20px #000;
}

.header-right-side {
  display: flex;
  align-items: center;
}

.profile-header-settings {
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  border-radius: 5px;
  border: 1px solid #fff;
  position: relative;
  cursor: pointer;
}

.profile-menu {
  position: absolute;
  right: 0;
  width: 120px;
  top: 40px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 5px 10px #000;
  transition: 0.2s linear height;

  z-index: 3;
}

.profile-menu-item > a {
  display: block;
  padding: 7px 5px 7px 15px;
  font-weight: normal;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
}

.profile-menu-item > a > svg {
  margin-right: 10px;
}

.open {
  height: 100px;
}

.closed {
  height: 0;
}
