.team-settings-container {
  padding: 50px;
}

.team-name-header {
  font-size: 4rem;
}

.invite-teammate-container {
  max-width: 500px;
  margin-top: 50px;
}

.secondary-settings-header {
  margin: 0 0 20px 0;
}

.user-mini {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 10px 0;
}

.user-mini-uuid {
  margin: 5px;
}

.user-mini-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;

  margin-right: 10px;
}

.user-invite-btn {
  background-color: rgb(86, 212, 47);
  font-size: 0.8rem;
  padding: 5px 10px;
  margin: 0 5px;
}

.user-withdraw-btn {
  background-color: rgb(212, 47, 47);
  font-size: 0.8rem;
  padding: 5px 10px;
  margin: 0 5px;
}

.disabled {
  background-color: #555;
}
