.app-create-team-form {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 200px;
}

.app-form {
  border-radius: 15px;
  padding: 30px;

  display: flex;
  flex-direction: column;
  align-items: center;

  position: relative;
}

.app-create-form {
  width: 100%;

}

.app-form-dark {
  background-color: #0b0e16;
  box-shadow: 0 0 10px #000;
}

.app-form-light {
  box-shadow: 0 0 10px rgb(165, 165, 165);
  background-color: #fff;
}
