.topics-input {
  display: flex;
  flex-direction: row;
  border-radius: 10px;
  border-radius: 10px;
  padding: 10px;
  width: 100%;
  margin-top: 5px;
  flex-wrap: wrap;
  justify-content: stretch;
}

label {
  display: block;
  padding-left: 30px;
  font-size: 1.2rem;
  align-self: flex-start;
}

.topics-input input {
  font-size: 1.3rem;
  border: none;
  outline: none;
  flex-grow: 1;
}

.topics-input-dark {
  background-color: #171d2d;
  box-shadow: 2px 2px 5px #000;
}

.topics-input-light {
  background-color: #fff;
  box-shadow: 2px 2px 5px #ccc;
}

.topics-input-dark input {
  background-color: #171d2d;
  color: #ccc;
}

.topics-input-light input {
  background-color: #fff;
}


.topics-list {
  display: flex;
}


.topic {
  font-size: 0.8rem;
  border-radius: 10px;
  border: 1px solid #fff;
  padding: 5px 10px;
  margin-right: 5px;
  color: #fff;
}

.topic span {
  margin-right: 10px;
}

.icon {
  font-size: 0.6rem;
  cursor: pointer;
}
