.form-field {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}

.form-field input {
  font-size: 1.3rem;
  border: 1px solid transparent;
  outline: none;
  border-radius: 10px;
  padding: 10px 30px;
  background: transparent;
  margin: 5px 0 10px 0;
  width: 100%;
}

.form-field label {
  display: block;
  padding-left: 30px;
  font-size: 1.2rem;
}

.form-field span {
  color: rgb(204, 30, 30);
  font-weight: bold;
  white-space: pre;
  padding-left: 30px;
}

.form-field-dark input {
  background-color: #171d2d;
  box-shadow: 2px 2px 5px #000;
  color: #eee;
}

.form-field-light input {
  background-color: #fff;
  box-shadow: 2px 2px 5px #ccc;
}
