.team-page-main-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 90vh;
}

.messages-container {
  border-radius: 20px;
  height: 85vh;
  width: 60vw;

  padding: 20px;
  margin: 30px;
  position: relative;
}

.messages-container-dark {
  background-color: #080a0f;
  box-shadow: 0 0 5px #000 inset;
}

.messages-container-light {
  background-color: #f7f7f7;
  box-shadow: 0 0 5px rgb(95, 95, 95) inset;
}

.messages-list {
  width: 100%;
  height: 95%;

  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-width: none;

  overflow: auto;
  -ms-overflow-style: none; /* IE 11 */
  scrollbar-width: none; /* Firefox 64 */

  display: flex;
  flex-direction: column;
}

.messages-list::-webkit-scrollbar {
  display: none;
}

.messages-list li {
  display: inline;
  margin: 0 10px;
}

.messages-input {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.messages-input input {
  width: 100%;
  font-size: 1.1rem;
  border-radius: 20px;
  border: none;
  outline: none;
  padding: 10px 25px;
  border: 2px solid #673ab7;
}

.messages-input-dark input {
  background-color: #0a0d14;
  color: #eee;
}

.messages-input-light {
}

.send-btn {
  position: absolute;
  bottom: -5px;
  right: 0;
  background: #673ab7;
  border: none;
  outline: none;
  border-radius: 30px;
  width: 45px;
  text-align: center;
  padding: 0;
  height: 45px;
}

.team-about-section {
  align-self: flex-start;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-grow: 1;

  padding: 30px 0;
}

.team-name {
  margin: 20px 0;
  display: flex;
  align-items: center;
}

.team-settings-btn {
  margin: 20px;
  font-size: 1.4rem;
  padding: 10px 15px;
  box-shadow: 0 0 10px #000;
  border-radius: 20px;
  cursor: pointer;
}

.user-msg-img {
  background-size: cover;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-position: center;
}

.user-msg-name {
  font-weight: bold;
}

.try-call-btn {
  margin-left: 10px;
  color: rgb(14, 189, 81);
  cursor: pointer;
}
