.app-login {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px 0 100px 0;

  background-repeat: no-repeat;
}

.app-form {
  border-radius: 15px;
  padding: 30px;

  display: flex;
  flex-direction: column;
  align-items: center;

  position: relative;
}

.app-form-dark {
  background-color: #0b0e16;
  box-shadow: 0 0 10px #000;
}

.app-form-light {
  box-shadow: 0 0 10px rgb(165, 165, 165);
  background-color: #fff;
}
